<script>
  import CustomMap from "@/components/CustomMap";

  export default {
    name: 'Home',

    components: {
      CustomMap,
    },
    computed: {
      events () {
        const todayMs = new Date().getTime()

        const dayLongMs = 86_400_000

        let sum = 120000

        let events = []

        for (let i = 0; i < 28 - new Date().getDay(); i++) {
          let tempSum = sum
          let tempColor = 'red'

          if (i > 3) {
            tempSum -= 20000
            tempColor = 'amber'
          }

          if (i > 7) {
            tempSum -= 30000
            tempColor = 'light-green'
          }

          events.push({
            name: `${tempSum}`,
            start: new Date(todayMs + i * dayLongMs),
            end: new Date(todayMs + i * dayLongMs),
            timed: false,
            color: tempColor
          })
        }

        return events
      }
    }
  }
</script>

